<template>
    <div class="homelista">
      <div class="list-box">
      </div>
      <div id="mine"></div>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        name: "HomeLista",
      props:['b'],
        data() {
            return {
              a:10,
              str:['首頁','首頁','首頁','首頁','首頁','首頁']
            }
        },

      mounted() {

        // let this_ = this;
        let myChart = echarts.init(document.getElementById('mine'));
        let option = {
          color: ['#f44'],
          tooltip : {
            trigger: 'axis',
            axisPointer : {
              type : 'shadow'
            }
          },
          xAxis : [
            {
              type : 'category',
              data : ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月',],
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis : [
            {
              type : 'value'
            }
          ],
          series : [
            {
              name:'每月花费',
              type:'bar',
              barWidth: '60%',
              data:[995,666,444,858,654,236,645,546,846,225,547,356]
            }
          ]
        };
        myChart.setOption(option);

        //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
        window.addEventListener('resize',function() {myChart.resize()});
      },
      methods: {},
      watch: {},
      created() {

      },
      beforeUpdate(){
        // debugger
      },
    }

</script>

<style lang="stylus" scoped>
  #mine
    width:100%
    height: 800px;
    border: 1px solid red;
    margin: 50px auto 0;
.list-box
  display: flex
  width:600px
  margin:0 auto
  .el-tag
    width:80px
    height:40px
    margin: 0 10px
    text-align: center
    line-height: 40px
    /*background-color: #4eff82*/
</style>



