import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import HomeLista from "../components/home/HomeLista";
import HomeListb from "../components/home/HomeListb";
import HomeListc from "../components/home/HomeListc";
import HomeListd from "../components/home/HomeListd";
import HomeListe from "../components/home/HomeListe";
// import HomeListf from "../components/home/HomeListf";
// import HomeListg from "../components/home/HomeListg";

// console.log(HomeLista);
// console.log(HomeListb);
// console.log(HomeListc);
// console.log(HomeListd);
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children:[
        {path:'homelista',component:HomeLista},
        {path:'homelistb',component:HomeListb},
        {path:'homelistc',component:HomeListc},
        {path:'homelistd',component:HomeListd},
        {path:'HomeListe',component:HomeListe},
        // {path:'/HomeListf',component:HomeListf},
        // {path:'/HomeListg',component:HomeListg}
      ]
    },
    // {path:'/home/lista',name:'NewFile',component:HomeLista}
  ]
})



